.bg-image-swiper {
  @include screen-lg{
    height: 733px;
    background-image: url('../images/bg_share.jpg');
    background-position: center center;
    background-size: cover;
  }
}

// swiper-wrapper 
.height-429 {
  @include screen-lg{
    height: 429px;
  }
}

// 
.swipe-img {
  position: relative;
  width: 100%;
  height: 100%;
  @include screen-lg{
    width: 60%;
    height: 90%;
  }
  img {
    width: 100%;
    height: 213px;
    object-fit: cover;
    border-radius: 24px;
    @include screen-lg{
      height: 100%;
    }

    &~.img-border {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 10px;
      left: -10px;
      z-index: -1;
      border: 1px solid #7C7E77;
      border-radius: 24px;
    }
  }
}

//  swiper  border hidden
.overflow-visible {
  overflow: visible;
}

// 
.swipe-card-text {
  position: relative;
  bottom: 0;
  right: 0;
  padding: 24px;
  margin-top: 42px;
  background-color: #fff;
  width: 100%;
  border-radius: 24px 24px 24px 0;
  @include screen-lg{
    position: absolute;
    padding: 32px;
    width: 45%;
  }

  &-img {
    position: absolute;
    top: -30px;
    right: 24px;
    max-width: 80px;
    border-radius: 50%;
    @include screen-lg{
      max-width: 100px;
      top: -50px;
      right: 32px;
    }
  }
}

// 
.swiper-buttons{
  position: initial;
  bottom: 0;
  left: 30%;
  display: flex;
  justify-content: center;
  margin-top: 32px;
  @include screen-lg{
    position: absolute;
    margin: 0;
    transform: translate(-50%);
  }
}
// 
.swiper-button-prev{
  position: unset;
  width: 24px;
  height: 24px;
  margin-top: 0;
}
.swiper-button-prev::after {
  content: ""; //
  font-size: 0; // icon 
}

// 
.swiper-button-next{
  position: unset;
  width: 24px;
  height: 24px;
  margin-top: 0;
}
.swiper-button-next::after {
  content: ""; //
  font-size: 0; // icon 
}

//  
.swiper-buttons{
  .swiper-pagination-custom {
    position: unset;
    width: auto;
  }
}
.swiper-pagination-bullet {
  background-color: #62DB54;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  width: 16px;
  border-radius: 8px;
  transition: width .2s ease;
}

// 
.text-green {
  color: #62DB54;
}

// 
.icon-size {
  width: 24px;
  height: 24px;
}

//  swiper
.swiper-share{
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

//  swiper
.swiper-class{
  margin-left: auto;
  margin-right: auto;
  position: relative;
  list-style: none;
  padding: 0;
  z-index: 1;
}